
import ChinaAddressData from 'vux/src/datas/china_address.json'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'


export default {
name: "Address",
data() {
return {
disabled: true,
index: 0,
addressData: ChinaAddressV4Data,
addressValue: ['北京市', '市辖区', '东城区'],
addressSendTag: ["公司", "家庭"],
addressReceiveTag: ["朋友", "客户"],
address: {
name: "",
phone: "",
street: [],
streetValue: ["110000", "110100", "110101"],
detail: "",
ismine: false,
isdefault: false
},
contact: {
name: "",
phone: "",
street: [],
streetValue: ["110000", "110100", "110101"],
detail: "",
ismine: false,
isdefault: false
},
id: ""
}
},
methods: {
/* isMine是否保存的是我的地址 */
addressSave: function (isMine) {
if(this.id.toString().length != 1){
this.update(isMine);
}
else{
this.address.ismine = true;
this.contact.ismine = false;
var data = isMine ? this.address : this.contact;
this.save(data);
}
},
save(data) {
this.axios.post("/delivery/address/save", data)
.then(response => {
if (response.data.errcode == 0) {
this.$router.push('/');
}
})
},
update(isMine){
var data= isMine ? this.address:this.contact;
this.axios.post("/delivery/address/update", data)
.then(response => {
if (response.data.errcode == 0) {
this.$router.push('/');
}
})
},
clearAddress() {
this.address.name = "";
this.address.phone = "";
this.address.street = [];
this.address.detail = "";
this.address.ismine = false;
this.address.isdefault = false;
},
getAddressById() {
this.axios.post("").then(reponse => {
var {data} = reponse;
});
},
onaddressChange(ids, names) {
this.address.street = names;
},
onContactChange(ids, names) {
this.contact.street = names;
}
},
mounted() {
var id = this.$route.params.id;
this.id = id;
if(id==="1") {
setTimeout(()=>{
this.index=0;
},1);
}
else if(id==="2"){
setTimeout(()=>{
this.index=1;
},1);
}
else {
this.axios.post("/delivery/address/getbyid", {id: id}).then(response => {
var data = response.data.data;
var ismine = data.ismine;
if (ismine) {
this.address = data;
this.index = 0
}
else {
this.contact = data;
this.index = 1
}

});
}
}
}



