export default {
  name: "Message",
  data() {
    return {
      type: '1',
      show: false,
      desc: "",
      nickname: "",
      showLeaveMessage: false,
      messageContent: "",
      list: []
    }
  },
  mounted() {
    this.getLeaveMessage();
  },
  methods: {
    showMessagwDetial(item) {
      this.show = true;
      this.desc = item.desc;
    },
    setleaveMessage() {
      this.showLeaveMessage = true;
    },
    closeLeaveMessage() {
      this.showLeaveMessage = false;
      this.messageContent = "";
    },
    saveLeaveMessage() {
      this.axios.post("/message/save", {
        desc: this.messageContent
      }).then(response => {
        this.getLeaveMessage();
        this.closeLeaveMessage();
      });
    },
    getLeaveMessage() {
      this.axios
        .get("/message/get")
        .then(response => {
          this.list=response.data.data;
        })

    }
  }
}



